.footer {
  padding-top: r(87);
  color: $colorWhite;
  background-color: $textColor;
  &__main {
    padding-bottom: r(140);
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    grid-gap: r(20);
    flex-wrap: wrap;
  }
  &__logo {
    display: flex;
    width: 100%;
    max-width: r(482);
    @include max-laptop {
      max-width: r(200);
    }
    picture {
      display: flex;
      font-size: 0;
    }
    img {
      width: 100%;
    }
  }
  &__right {
    display: flex;
    align-items: flex-start;
    grid-gap: r(15);
    width: 100%;
    max-width: r(680);
    @include max-small-portrait {
      flex-direction: column;
      grid-gap: r(35);
    }
  }
  &__col {
    flex: 1;
  }
  &__nav {
    ul {
      display: grid;
      grid-gap: r(35);
      list-style: none;
      li {
        a {
          font-weight: bold;
          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
  }
  &__title-col {
    font-weight: bold;
    margin-bottom: r(35);
  }
  &__find {
    display: grid;
    grid-gap: r(15);
  }
  &__bottom {
    border-top: r(1) solid $colorYellow;
    padding: r(30) 0;
    .footer {
      &__wrapper {
        align-items: center;
      }
    }
  }
  &__powered {
    a {
      font-weight: bold;
      color: currentColor;
    }
  }
  a {
    color: currentColor;
  }
}

.contact-call + .footer {
  padding-top: r(192);
  margin-top: r(-87);
}