html {
	-webkit-tap-highlight-color: transparent !important;
	font-size: 16px;

	@include min-tablet {
		font-size: 1.3vw;
	}
	@include min-laptop {
		font-size: 0.826vw;
	}
}


body.admin-bar {
	padding-top: 32px;
	@include max-tablet {
		padding-top: 46px;
	}
	.header {
		//position: fixed;
		top: 32px;
		@include max-tablet {
			top: 46px;
		}
	}
}

body {
	font-family: 'Montserrat', sans-serif;
	font-size: r(20);
	//background-color: #F7F7F7;
}

[class*="__container"] {
	width: 100%;
	max-width: r(1566);
	padding-inline: r(15);
	margin-inline: auto;
}

.container {
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
	padding: 0 r(12);

	&__test {
	  padding: 0 r(12);
	}
}

.main-menu {
	display: flex;
	list-style: none;
}

a {
	color: $textColor;
}

.icon {
	fill: currentColor;
	//stroke: currentColor;
}

h2 {
	font-weight: bold;
	font-size: r(52.7);
	line-height: 1;
	@include max-tablet {
		font-size: r(36);
	}
}

.text-center {
	text-align: center;
}

.button-social {
	display: inline-flex;
	align-items: center;
	grid-gap: r(10);
	color: $colorWhite;
	font-weight: 600;
	font-size: r(18);
	text-decoration: none;
	white-space: nowrap;
	svg {
		width: r(23);
		height: r(23);
	}
	&:hover {
		opacity: 0.7;
	}
}

.button-global {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	min-height: r(50);
	min-width: r(172);
	padding: r(10) r(20);
	color: $colorBrown;
	font-weight: 600;
	font-size: r(20);
	border: none;
	background-color: $colorYellow;
	border-radius: r(50);
	transition: .3s;
	white-space: nowrap;
	&:hover {
		color: $colorWhite;
		text-decoration: none;
		background-color: $colorOrange;
	}
}

.button-call {
	display: inline-flex;
	align-items: center;
	grid-gap: r(20);
	text-decoration: none;
	color: currentColor;
	white-space: nowrap;
	svg {
		flex-shrink: 0;
		width: r(52);
		height: r(52);
		@include max-tablet {
			width: r(24);
			height: r(24);
		}
	}
	&__main {
		display: flex;
		flex-direction: column;
		grid-gap: r(8);
	}
	span {}
	p{
		font-weight: 700;
		font-size: r(24);
		@include max-tablet {
			font-size: r(20);
		}
	}
	&:hover {
		p{
			text-decoration: underline;
		}
	}
}

.button-menu {
	display: none;
	position: relative;
	width: r(30);
	height: r(30);
	border: none;
	background-color: transparent;
	align-items: center;
	justify-content: center;
	color: $colorOrange;
	@include max-tablet {
		display: flex;
	}
	&:before {
		content: '';
		display: block;
		position: absolute;
		width: 100%;
		height: 2px;
		background-color: currentColor;
		box-shadow: 0 7px 0 0 currentColor;
		transform: translateY(-7px);
		transition: 0.3s;
	}
	&:after {
		content: '';
		display: block;
		position: absolute;
		width: 100%;
		height: 2px;
		background-color: currentColor;
		transform: translateY(7px);
		transition: 0.3s;
	}
	&:hover {
		cursor: pointer;
	}
}

body.menu-open {
	overflow: hidden;
	.button-menu {
		&:before {
			box-shadow: none;
			transform: translateY(0) rotate(45deg);
		}
		&:after {
			transform: translateY(0) rotate(-45deg);
		}
	}
}

.typography-content {
	p,ul,ol,table,blockquote {
		margin-bottom: r(15);
	}
	ul,ol{
		padding-left: r(30);
	}
	*:last-child {
		margin: 0;
	}
}